export default function Section({
  introTitle,
  introText,
  introDesc,
  introButton,
  name,
  img,
  role
}) {
  return (
    <div className="Section">
      <div className="Introduction">
        <div className="introText ">
          {/* {introTitle && <h1 className="m-auto">About Us</h1>} */}
          <h2>
            {introText}
            {name && <span>{" " + name}</span>}
          </h2>
          {role && <h2>a Web Developer</h2>}
          <p>{introDesc}</p>
          <div className="introButtons ">
            {introButton && (
              <button className="introButtons">{introButton}</button>
            )}
          </div>
        </div>

        <div className=" introImage ">
          <img src={img} alt="" />
        </div>
      </div>
    </div>
  );
}
