export default function ContactUs() {
  return (
    <div className="ContactUs">
      <h2 className="text-center">Contact Us</h2>
      <div className="ContactUsrow ">
        <div className="contactForm">
          <form>
            <div className="form-group">
              <label for="exampleFormControlInput1">Full Name</label>
              <input
                type="name"
                className="form-control"
                id="exampleFormControlInput1"
                placeholder="Your Name"
              />
              <label for="exampleFormControlInput1">Phone No.</label>
              <input
                type="phone"
                className="form-control"
                id="exampleFormControlInput1"
                placeholder="+91..."
              />
              <label for="exampleFormControlInput1">Email address</label>
              <input
                type="email"
                className="form-control"
                id="exampleFormControlInput1"
                placeholder="name@example.com"
              />
            </div>

            <div className="form-group">
              <label for="exampleFormControlTextarea1">
                Describe your query
              </label>
              <textarea
                className="form-control"
                id="exampleFormControlTextarea1"
                rows="3"
              ></textarea>
            </div>
          </form>
          <div className="contactButtons">
            <button>Submit</button>
          </div>
        </div>
        <div className="contactImage">
          <img
            src="https://image.freepik.com/free-vector/email-marketing-internet-chatting-24-hours-support-get-touch-initiate-contact-contact-us-feedback-online-form-talk-customers-concept_335657-25.jpg"
            alt=""
          />
        </div>
      </div>
    </div>
  );
}
